import { IonAlert } from '@ionic/react'
import { LatLngTuple } from 'leaflet'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useRef, useState } from 'react'
import * as FIcon from 'react-feather'

import { AdminItemField, AdminItemFieldRegistro, Page, SelectorModal, SMap } from '../../components'
import FooterFormulario from '../../components/FooterFormulario'
import MapaSelectorModal from '../../components/MapaSelectorModal'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS_APP_FABRICANTE } from '../../constants'
import {
  autocompletar_calles,
  DetailViewProps,
  isMovil,
  setearSoloCalle,
  useOpenPage,
  useToaster,
} from '../../helpers-ts'
import { ICONOS } from '../../helpers-ts/iconos'
import { useStores } from '../../models/root-store/root-store-context'
import { PuntoRetiro } from '../../services/api'
import './FabricantePuntoRetiroUpdate.css'

export const FabricantePuntoRetiroUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [PuntoRetiro, setPuntoRetiro] = useState<PuntoRetiro>({} as any)
  const [Errors, setErrors] = useState<PuntoRetiro | null>(null)
  const IdPuntoRetiro = match.params.id
  const [Loading, setLoading] = useState<boolean>(false)
  const [Alert, setAlert] = useState(false)
  const addToast = useToaster()
  const [Ready, setReady] = useState(false)
  const openPage = useOpenPage()
  const Movil = isMovil()

  // Ubicación
  const MapaRef = useRef() // Para el modal
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 } as any)
  const [Lat, setLat] = useState<number | null>(null)
  const [Lng, setLng] = useState<number | null>(null)
  const [UbicacionUser, setUbicacionUser] = useState<LatLngTuple | null>(null)

  React.useEffect(() => {
    if (IdPuntoRetiro) {
      setLoading(true)
      obtenerPuntoRetiro(parseInt(IdPuntoRetiro))
    } else {
      setTimeout(() => {
        setReady(true)
      }, 700)
    }
  }, [IdPuntoRetiro, window.location.pathname])


  const obtenerPuntoRetiro = (idProducto: number) => {
    rootStore.environment.api.adminGetPuntoRetiro(idProducto).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        const datos = { ...result.punto_retiro } as any
        datos.calle_principal = { label: datos.calle_principal, value: datos.calle_principal }
        datos.calle_secundaria = { label: datos.calle_secundaria, value: datos.calle_secundaria }
        setPuntoRetiro(datos)
        const ubicacion = result.punto_retiro.ubicacion.split(',')
        const latitud = parseFloat(ubicacion[0])
        const longitud = parseFloat(ubicacion[1])
        setLat(latitud)
        setLng(longitud)
        setCenter({ lat: latitud, lng: longitud })
        setReady(true)
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  const setDato = (campo: string, valor: string) => {
    let datos = { ...PuntoRetiro }
    // @ts-ignore
    datos[campo] = valor
    setPuntoRetiro(datos as any as PuntoRetiro)
  }

  const dataSelectorModalCiudad = (data: any) => {
    setDato('ciudad', data[0])
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const seleccionarUbicacion = (lat: any, lng: any) => {
    setLat(lat)
    setLng(lng)
    setCenter([lat, lng])
  }

  const guardar = () => {
    setErrors(null)
    if (PuntoRetiro) {
      setLoading(true)
      const ubicacion = Lat + ',' + Lng
      const calle_principal = PuntoRetiro.calle_principal as any
      const calle_secundaria = PuntoRetiro.calle_secundaria as any
      const datos = {
        ...PuntoRetiro,
        fabricante: loginStore.fabricante_id,
        ubicacion: ubicacion,
        calle_principal: calle_principal ? calle_principal.value : '',
        calle_secundaria: calle_secundaria ? calle_secundaria.value : '',
      }
      if (typeof datos.ciudad === 'object') {
        datos.ciudad = (datos.ciudad as any).id
      }
      rootStore.environment.api.guardarPuntoRetiro(datos).then((result: any) => {
        setLoading(false)
        if (result.kind === 'ok') {
          addToast('Punto de retiro guardado exitosamente', 'success')
          obtenerFabricante()
          setPuntoRetiro({} as any)
          history.push(RUTAS_APP_FABRICANTE.PUNTOS_RETIRO)
        } else if (result.kind === 'bad-data') {
          setErrors(result.errors)
          if (result.errors.non_field_errors) {
            addToast(result.errors.non_field_errors[0], 'warning')
          }
        } else {
          addToast('Ocurrió un error al guardar vuelva a intentar en unos momentos', 'error')
        }
      })
    } else {
      addToast('Complete todos los campos para continuar', 'warning')
    }
  }

  const obtenerFabricante = () => {
    if (loginStore.fabricante_id) {
      rootStore.environment.api.adminGetFabricante(loginStore.fabricante_id).then((result) => {
        if (result.kind === 'ok') {
          loginStore.setFabricante(result.fabricante)
        }
      })
    }
  }

  const eliminarPuntoRetiro = () => {
    rootStore.environment.api.eliminarPuntoRetiro(parseInt(IdPuntoRetiro)).then((result: any) => {
      setLoading(false)
      if (result.kind === 'ok') {
        addToast('Punto de retiro eliminado satisfactoriamente', 'success')
        obtenerFabricante()
        setPuntoRetiro({} as any)
        history.goBack()
      } else {
        addToast('Ocurrió un error al eliminar vuelva a intentar en unos momentos', 'error')
      }
    })
  }

  useEffect(() => {
    if (Ready && Lat && Lng) {
      rootStore.environment.api.infoCalle(Lat, Lng).then((result: any) => {
        if (result.kind === 'ok') {
          if (result.response.info) {
            const i = result.response.info
            const dato: any = { ...PuntoRetiro }
            if (i.calle) {
              dato.calle_principal = { value: i.calle, label: i.calle }
            }
            if (i.ciudad_obj) {
              dato.ciudad = rootStore.ciudades.find((c) => c.id === i.ciudad_obj.id)
            }
            if (i.secondary) {
              dato.calle_secundaria = { value: i.secondary, label: i.secondary }
            }
            setPuntoRetiro(dato)
          }
        } else {
          console.log('error infoCalle', result)
        }
      })
    }
  }, [Lat, Lng])

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'punto-retiro-editar-fabricante'}>
      <MapaSelectorModal ref={MapaRef} lat={Lat} lng={Lng} onAccept={seleccionarUbicacion} />
      <SubHeader
        noContainer
        links={[
          { titulo: 'Perfil', ruta: RUTAS_APP_FABRICANTE.PERFIL },
          { titulo: 'Puntos de retiro', ruta: RUTAS_APP_FABRICANTE.PUNTOS_RETIRO },
          { titulo: PuntoRetiro.id ? PuntoRetiro.nombre : 'Crear punto de retiro' },
        ]}
      />
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={'Eliminar'}
        message={'Confirma que desea eliminar el punto de retiro?'}
        buttons={[
          {
            text: 'Confirmar',
            handler: () => eliminarPuntoRetiro(),
          },
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
        ]}
      />
      <div className={Movil ? 'container-lg' : ''}>
        <div id="punto-retiro-container" className={' d-flex flex-column'} style={{ height: 'fit-content' }}>
          <div className="titulo">
            {ICONOS.punto_retiro} {PuntoRetiro.id ? PuntoRetiro.nombre : "Crear punto de retiro"}
          </div>

          <div className='row'>
            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Nombre'}
              titulo_externo={'Nombre'}
              tipo={'texto'}
              campo={'nombre'}
              valor={PuntoRetiro ? PuntoRetiro.nombre : ''}
              error={Errors ? Errors.nombre : null}
              set_dato={setDatoInput}
              columnas_sm={12}
              columnas={12}
            />
          </div>

          <div className={'row'}>
            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Calle principal'}
              tipo={'select2async'}
              campo={'calle_principal'}
              valor={PuntoRetiro ? PuntoRetiro.calle_principal : ''}
              error={Errors && Errors.calle_principal}
              set_dato={(campo: string, data: any) => {
                if (!data) {
                  return
                }
                setDato(campo, data)
              }}
              select_placeholder={'Calle principal'}
              select_onchange={(valor: any) => {
                setearSoloCalle(rootStore, setDato, setPuntoRetiro, PuntoRetiro, 'calle_principal', valor)
              }}
              opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
              select_no_options_text={'No se encuentran sugerencias'}
              columnas={12}
              columnas_sm={12}
            />
          </div>
          <div className={'row'}>
            <AdminItemField
              deshabilitado={!Loading}
              titulo={'Calle secundaria'}
              tipo={'select2async'}
              campo={'calle_secundaria'}
              valor={PuntoRetiro ? PuntoRetiro.calle_secundaria : ''}
              error={Errors && Errors.calle_secundaria}
              set_dato={(campo: string, data: any) => {
                if (!data) {
                  return
                }
                setDato(campo, data)
              }}
              select_placeholder={'Calle secundaria'}
              select_onchange={(valor: any) => {
                setearSoloCalle(rootStore, setDato, setPuntoRetiro, PuntoRetiro, 'calle_secundaria', valor)
              }}
              opciones_select={(text: string) => autocompletar_calles(rootStore, UbicacionUser, text)}
              select_no_options_text={'No se encuentran sugerencias'}
              columnas={12}
              columnas_sm={12}
            />
          </div>

          <div className={'row'}>
            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Número'}
              titulo_externo={'Número del local'}
              tipo={'texto'}
              campo={'numero'}
              valor={PuntoRetiro ? PuntoRetiro.numero : ''}
              error={Errors ? Errors.numero : null}
              set_dato={setDatoInput}
              columnas_sm={6}
              columnas={12}
            />
            <div className="col-12 col-lg-6">
              <SelectorModal
                deshabilitado={!Loading}
                titulo={'Ciudad:'}
                titulo_label={'Seleccioná la ciudad'}
                listado_elementos={rootStore.ciudades}
                listado_elementos_seleccionados={PuntoRetiro && PuntoRetiro.ciudad ? PuntoRetiro.ciudad : null}
                datos_filtrados={dataSelectorModalCiudad}
                multiple={false}
                llave_label_opcion={'nombre'}
              />
              {Errors && Errors.ciudad && <div className={'text-danger'}>{Errors.ciudad}</div>}
            </div>

            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Referencia'}
              titulo_externo={'Referencia'}
              tipo={'textarea'}
              campo={'referencia'}
              valor={PuntoRetiro ? PuntoRetiro.referencia : ''}
              error={Errors ? Errors.referencia : null}
              set_dato={setDatoInput}
              columnas={12}
            />

            <AdminItemFieldRegistro
              deshabilitado={!Loading}
              titulo={'Teléfono'}
              titulo_externo={'Teléfono:'}
              tipo={'texto'}
              campo={'nro_telefono'}
              valor={PuntoRetiro ? PuntoRetiro.nro_telefono : ''}
              error={Errors ? Errors.nro_telefono : null}
              set_dato={setDatoInput}
              columnas={12}
            />
          </div>
          <div className={'col-12'}>
            <div className={'row my-2'}>
              {Ready && (
                <div className={'col-12'} style={{ height: 300, width: '100%', cursor: 'pointer' }}>
                  <SMap
                    lat={Lat}
                    lng={Lng}
                    zoom={Lat ? 16 : 14}
                    center={Center}
                    bloquear_interaccion
                    onClick={() => {
                      setTimeout(() => {
                        ;(MapaRef.current as any).open()
                      }, 300)
                    }}
                  />
                </div>
              )}
            </div>
          </div>
            
          <AdminItemFieldRegistro
            deshabilitado={!Loading}
            titulo={'Punto de retiro por defecto'}
            tipo={'boolean'}
            campo={'default'}
            valor={PuntoRetiro ? PuntoRetiro.default : false}
            set_dato={setDatoInput}
            columnas={12}
          />
        </div>
        <div className="d-flex flex-row mb-4">
          {PuntoRetiro && PuntoRetiro.id && (
            <div
              className={'btn btn-danger px-4'}
              onClick={() => {
                setAlert(true)
              }}
            >
              Eliminar
            </div>
          )}
          <div
            className={'btn btn-primary px-4 ml-3'}
            onClick={() => {
              guardar()
            }}
          >
            Guardar
          </div>
        </div>
      </div>
    </Page>
  )
})

export default FabricantePuntoRetiroUpdate
